import React from "react";
import styled from "styled-components";
import Slider from "react-slick";

const CustomCarousel = styled.div`
  width: 100%;
  & .slick-slider {
    height: 100%;
    & .slick-prev {
      z-index: 40;
      left: -0.9rem;
      top: -2.4rem;
      padding-bottom: 2rem;
      &:before {
        font-size: 1rem;
        font-family: "Font Awesome 5 Free";
        font-weight: 900;
        color: grey;
        content: "\f053";
      }
    }
    & .slick-next {
      z-index: 40;
      right: -0.8rem;
      top: -2.4rem;
      padding-bottom: 2rem;
      &:before {
        font-size: 1rem;
        font-family: "Font Awesome 5 Free";
        font-weight: 900;
        color: grey;
        content: "\f054";
      }
    }
    & .slick-dots {
      z-index: 40;
      bottom: -2rem;
      & button {
        &:before {
          color: rgba(234, 88, 12, var(--tw-text-opacity));
        }
      }
    }

    & div:focus {
      outline: none;
    }
  }
`;

const TabTitleNoScrollBar = styled.ul`
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
  &::-webkit-scrollbar {
    display: none; /* Chrome, Safari, Opera*/
  }
`;

export default class Carousel extends React.Component<any> {
  tabScrollRef: React.RefObject<any>;
  tabItemRef1: React.RefObject<any>;
  tabItemRef2: React.RefObject<any>;
  tabItemRef3: React.RefObject<any>;
  tabItemRef4: React.RefObject<any>;
  tabItemRef5: React.RefObject<any>;
  tabItemRef6: React.RefObject<any>;
  tabItemRef7: React.RefObject<any>;
  tabItemRef8: React.RefObject<any>;
  tabItemRef9: React.RefObject<any>;
  tabItemRef10: React.RefObject<any>;
  tabItemRef11: React.RefObject<any>;
  tabItemRef12: React.RefObject<any>;
  tabItems: React.RefObject<any>[];
  constructor(props: any) {
    super(props);
    this.tabScrollRef = React.createRef();
    this.tabItemRef1 = React.createRef();
    this.tabItemRef2 = React.createRef();
    this.tabItemRef3 = React.createRef();
    this.tabItemRef4 = React.createRef();
    this.tabItemRef5 = React.createRef();
    this.tabItemRef6 = React.createRef();
    this.tabItemRef7 = React.createRef();
    this.tabItemRef8 = React.createRef();
    this.tabItemRef9 = React.createRef();
    this.tabItemRef10 = React.createRef();
    this.tabItemRef11 = React.createRef();
    this.tabItemRef12 = React.createRef();
    this.tabItems = [
      this.tabItemRef1,
      this.tabItemRef2,
      this.tabItemRef3,
      this.tabItemRef4,
      this.tabItemRef5,
      this.tabItemRef6,
      this.tabItemRef7,
      this.tabItemRef8,
      this.tabItemRef9,
      this.tabItemRef10,
      this.tabItemRef11,
      this.tabItemRef12,
    ];
  }

  state = {
    slideIndex: 0,
    carouselPg: 0,
    tabXPosition: 0,
  };

  slider: any;

  componentDidUpdate() {
    this.slider.slickGoTo(this.state.carouselPg);
  }

  render() {
    const data = this.props.data;
    const blackMode = this.props.blackMode;
    const settings = {
      dots: true,
      infinite: true,
      fade: true,
      speed: 10,
      slidesToShow: 1,
      slidesToScroll: 1,
      beforeChange: (current: any, next: any) => {
        this.tabItems[next].current.click();
      },
    };

    return (
      <section
        className={`my-12 py-20 transition duration-1000 ${
          blackMode ? "bg-gray-800" : "bg-white"
        }`}
      >
        <div className=" w-11/12 mx-auto">
          <h1
            className={`text-2xl font-bold leading-normal mb-7  transition duration-1000 ${
              blackMode ? " text-white text-shadow-lg" : "text-gray-900"
            }`}
          >
            {data.mainTitle.map((mainTitleVal: any) => (
              <>
                {mainTitleVal} <br />
              </>
            ))}
          </h1>
          <TabTitleNoScrollBar
            className=" w-11/12 mx-auto flex mb-6 overflow-x-auto relative z-20"
            ref={this.tabScrollRef}
          >
            {data.contents.map((contentsVal: any, contentsIdx: any) => (
              <div className="px-2">
                <li
                  className={`h-9 cursor-pointer transition duration-1000 w-max  ${
                    contentsIdx === this.state.carouselPg
                      ? "border-b-4 border-orange-500 font-bold"
                      : "border-b-4 border-transparent"
                  } ${
                    blackMode ? " text-white text-shadow-lg" : "text-gray-900"
                  } `}
                  ref={this.tabItems[contentsIdx]}
                  onClick={async () => {
                    await this.setState({ carouselPg: contentsIdx });
                    await this.setState({
                      tabXPosition:
                        this.tabItems[contentsIdx].current.offsetLeft -
                        window.innerWidth * 0.5 +
                        this.tabItems[contentsIdx].current.offsetWidth / 2,
                    });
                    this.tabScrollRef.current.scrollTo({
                      left: this.state.tabXPosition,
                      behavior: "smooth",
                    });
                  }}
                >
                  {contentsVal.subTitle}
                </li>
              </div>
            ))}
          </TabTitleNoScrollBar>

          <CustomCarousel>
            <Slider ref={(slider) => (this.slider = slider)} {...settings}>
              {data.contents.map((contentsVal: any, contentsIdx: any) => (
                <div>
                  <div className={` relative z-30`}>
                    <div
                      className="w-full flex justify-center mb-4"
                      style={
                        contentsIdx === this.state.carouselPg
                          ? { opacity: "100%", transition: "opacity 1s" }
                          : { opacity: "0%" }
                      }
                    >
                      <img
                        className=" rounded-xl"
                        src={contentsVal.src}
                        alt={contentsVal.subTitle}
                      ></img>
                    </div>

                    <div className="w-full flex items-center justify-center">
                      <div
                        className={`${"text-left"}`}
                        style={
                          contentsIdx === this.state.carouselPg
                            ? { opacity: "100%", transition: "opacity 1s 0.1s" }
                            : { opacity: "0%" }
                        }
                      >
                        <div
                          className={`text-2xl font-bold leading-normal mb-4 transition duration-1000  ${
                            blackMode
                              ? " text-white text-shadow-lg"
                              : "text-gray-900"
                          }`}
                        >
                          {contentsVal.h1.map((h1Val: any) => (
                            <>
                              {h1Val}
                              <br />
                            </>
                          ))}
                        </div>
                        <div
                          className={`transition duration-1000  ${
                            blackMode
                              ? " text-gray-300 text-shadow-lg"
                              : "text-gray-900"
                          }`}
                        >
                          {contentsVal.p.map((p1Val: any) => (
                            <>
                              {p1Val}
                              <br />
                            </>
                          ))}
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              ))}
            </Slider>
          </CustomCarousel>

          <div className="flex justify-center">{data.button}</div>
        </div>
      </section>
    );
  }
}
