import { Helmet } from "react-helmet";

export default function App() {
  return (
    <>
      <Helmet>
        <title>포케팅</title>
        <meta
          name="description"
          property="og:description"
          content="비주얼 인플루언서 마케팅, 키워드 블로그 체험단 마케팅, 바이럴 마케팅, 콘텐츠 마케팅, 브랜드 마케팅"
        />
      </Helmet>
      {isMobile ? <MobileView /> : <DesktopView />}
    </>
  );
}
