import React from "react";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect,
} from "react-router-dom";
import Test from "/home/po-main1-front/src/atomic/pages/Test";
import Introduce from "../atomic/pages/Introduce";
import Costomer_Visual_Health from "../atomic/pages/Costomer_visual_healthFood";
import Service_visualInfluencer from "../atomic/pages/Service_visualInfluencer";
import Service_massPosting from "../atomic/pages/Service_massPosting";
import Service_keywordBlog from "../atomic/pages/Service_keywordBlog";
import PricePlan from "/home/po-main1-front/src/atomic/pages/PricePlan";
import RouteToTop from "/home/po-main1-front/src/hooks/useRouteToTop";
import Header1 from "/home/po-main1-front/src/atomic/templates/Header1";
import Footer1 from "/home/po-main1-front/src/atomic/templates/Footer1";
import Main from "/home/po-main1-front/src/atomic/pages/Main";
import PaymentSuccess from "/home/po-main1-front/src/atomic/pages/PaymentSuccess";
import CustomQuote from "../atomic/pages/CustomQuote";
import Costomer_visual_child from "../atomic/pages/Costomer_visual_child";
import Costomer_visual_fitness from "../atomic/pages/Costomer_visual_fitness";
import Costomer_visual_healthFood from "../atomic/pages/Costomer_visual_healthFood";
import Costomer_visualMain from "../atomic/pages/Costomer_visualMain";

export default function App() {
  return (
    <Router>
      <RouteToTop />

      <Header1 />
      <Switch>
        {/* 서비스 소개 */}
        <Route path="/service/keyword-blog/introduce">
          <Service_keywordBlog></Service_keywordBlog>
        </Route>
        <Route path="/service/mass-posting/introduce">
          <Service_massPosting></Service_massPosting>
        </Route>
        <Route path="/service/visual-influencer/introduce">
          <Service_visualInfluencer></Service_visualInfluencer>
        </Route>

        {/* 고객 사례 */}
        <Route path="/costomer-story/visual-influencer/child">
          <Costomer_visual_child></Costomer_visual_child>
        </Route>
        <Route path="/costomer-story/visual-influencer/fitness">
          <Costomer_visual_fitness></Costomer_visual_fitness>
        </Route>
        <Route path="/costomer-story/visual-influencer/health-food">
          <Costomer_visual_healthFood></Costomer_visual_healthFood>
        </Route>
        <Route path="/costomer-story/visual-influencer">
          <Costomer_visualMain></Costomer_visualMain>
        </Route>
        <Route path="/costomer-story">
          <Redirect to="/costomer-story/visual-influencer" />
        </Route>

        {/* 메인 */}
        <Route path="/introduce">
          <Introduce></Introduce>
        </Route>
        <Route path="/price-plan/custom-quote">
          <CustomQuote></CustomQuote>
        </Route>
        <Route path="/price-plan/success">
          <PaymentSuccess></PaymentSuccess>
        </Route>
        <Route path="/price-plan">
          <PricePlan></PricePlan>
        </Route>
        <Route path="/home">
          <div>home</div>
        </Route>
        <Route path="/test">
          <Test></Test>
        </Route>
        <Route path="/">
          <Main></Main>
        </Route>
      </Switch>
      <Footer1 />
    </Router>
  );
}
