import React from "react";
import styled from "styled-components";
import Slider from "react-slick";

const CustomCarousel = styled.div`
  width: 100%;
  & .slick-slider {
    height: 100%;
    & .slick-prev {
      z-index: 10;
      left: -2.2rem;
      padding-bottom: 2rem;
      &:before {
        font-size: 3rem;
        font-family: "Font Awesome 5 Free";
        font-weight: 900;
        color: grey;
        content: "\f053";
      }
    }
    & .slick-next {
      z-index: 10;
      padding-bottom: 2rem;
      &:before {
        font-size: 3rem;
        font-family: "Font Awesome 5 Free";
        font-weight: 900;
        color: grey;
        content: "\f054";
      }
    }
    & .slick-dots {
      z-index: 10;
      bottom: 1rem;
      & button {
        &:before {
          color: black;
        }
      }
    }

    & div:focus {
      outline: none;
    }
  }
`;

export default class Carousel extends React.Component<any> {
  state = {
    slideIndex: 0,
    carouselPg: 0,
  };
  slider: any;

  componentDidUpdate() {
    this.slider.slickGoTo(this.state.carouselPg);
  }

  render() {
    const data = this.props.data;
    const blackMode = this.props.blackMode;
    const settings = {
      dots: false,
      infinite: true,
      fade: true,
      speed: 10,
      slidesToShow: 1,
      slidesToScroll: 1,
      beforeChange: (current: any, next: any) => {
        this.setState({ carouselPg: next });
      },
    };

    return (
      <section
        className={`my-20 py-20 transition duration-1000 ${
          blackMode ? "bg-gray-800" : "bg-white"
        }`}
      >
        <div className={`max-w-screen-lg mx-auto`}>
          <h1
            className={`text-3xl font-bold leading-normal mb-7 transition duration-1000  ${
              blackMode ? " text-white text-shadow-lg" : "text-gray-900"
            }`}
          >
            {data.mainTitle.map((mainTitleVal: any) => (
              <>
                {mainTitleVal} <br />
              </>
            ))}
          </h1>

          <ul className="flex mb-6">
            {data.contents.map((contentsVal: any, contentsIdx: any) => (
              <>
                <li
                  key={contentsIdx}
                  className={`mr-5 h-9 cursor-pointer transition duration-1000  ${
                    contentsIdx === this.state.carouselPg
                      ? "border-b-4 border-orange-500 font-bold"
                      : "border-b-4 border-transparent"
                  } ${
                    blackMode ? " text-white text-shadow-lg" : "text-gray-900"
                  } `}
                  onClick={() => {
                    this.setState({ carouselPg: contentsIdx });
                  }}
                >
                  {contentsVal.subTitle}
                </li>
              </>
            ))}
          </ul>

          <CustomCarousel style={{ height: data.height }}>
            <Slider ref={(slider) => (this.slider = slider)} {...settings}>
              {data.contents.map((contentsVal: any, contentsIdx: any) => (
                <div>
                  <div
                    className={`flex relative z-30 ${
                      contentsIdx % 2 === 0 ? "flex-row" : "flex-row"
                    }`}
                  >
                    <div
                      className="w-1/2 flex justify-center"
                      style={
                        contentsIdx === this.state.carouselPg
                          ? { opacity: "100%", transition: "opacity 1s" }
                          : { opacity: "0%" }
                      }
                    >
                      <div className="">
                        <img
                          className=" rounded-xl"
                          src={contentsVal.src}
                          style={{ height: data.height }}
                          alt={contentsVal.subTitle}
                        ></img>
                      </div>
                    </div>

                    <div className="w-1/2 flex items-center justify-center">
                      <div
                        className={`${
                          contentsIdx % 2 === 0 ? "text-left" : "text-left"
                        }`}
                        style={
                          contentsIdx === this.state.carouselPg
                            ? { opacity: "100%", transition: "opacity 1s 0.2s" }
                            : { opacity: "0%" }
                        }
                      >
                        <div
                          className={`text-3xl font-bold leading-normal mb-4 transition duration-1000  ${
                            blackMode
                              ? " text-white text-shadow-lg"
                              : "text-gray-900"
                          }`}
                        >
                          {contentsVal.h1.map((h1Val: any) => (
                            <>
                              {h1Val}
                              <br />
                            </>
                          ))}
                        </div>
                        <div
                          className={`transition duration-1000  ${
                            blackMode
                              ? " text-gray-300 text-shadow-lg"
                              : "text-gray-900"
                          }`}
                        >
                          {contentsVal.p.map((p1Val: any) => (
                            <>
                              {p1Val}
                              <br />
                            </>
                          ))}
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              ))}
            </Slider>
          </CustomCarousel>
          <div className="flex justify-center">{data.button}</div>
        </div>
      </section>
    );
  }
}
