import { useState } from "react";

const App = (initialValue: any) => {
  const [value, setValue] = useState(initialValue);
  const checkToggle = () => {
    value ? setValue(false) : setValue(true);
  };
  const onClick = () => {
    checkToggle();
  };
  return { value, onClick };
};

export default App;

